// This mixin is broken per 1 March 2016
.button {
    cursor: pointer;
    border: 2px solid $color-link;
    color: $color-link;
    display: inline-block;
    padding: to-rem(4px);
}


@mixin button($background: light) {
    $button-color: $color-purple;
    $light-button-color: $color-pink;

    @if $background == dark {
        border-color: $light-button-color;
        color: $light-button-color;

        &:hover, &:active {
            background-color: $light-button-color;
            border-color: $light-button-color;
            color: $color-text;
        }
    } @else {
        border-color: $button-color;
        color: $button-color;

        &:hover, &:active {
            background-color: $button-color;
            border-color: $button-color;
            color: $color-text-light;
        }
    }

    border-width: to_rem(2px);
    border-style: solid;
    padding-left: $half-column-gutter;
    padding-right: $half-column-gutter;
    border-radius: to_rem(6px);
    padding-left: to_rem(6px);
    padding-right: to_rem(6px);
    transition:
        background-color 800ms ease-out,
        background-image 800ms ease-out,
        color 800ms ease-out;
    cursor: pointer;
    user-select: none;
    text-align: center;

    &:hover {
        transition:
            background-color 100ms ease-in,
            background-image 100ms ease-in,
            color 100ms ease-in;
    }

    &:active {
        position: relative;
        top: to_rem(2px);
    }
}

@mixin focus-box($color: $default-focus-box-color) {
    border-width: to_rem(2px);
    border-style: solid;
    padding-left: $half-column-gutter;
    padding-right: $half-column-gutter;
    @include rhythm-paddings(.25);
    @include rhythm-margins(.5);

    // Border fallback
    border-left-color: $color;
    border-bottom-color: $color;
    border-right-color: transparent;
    border-top-color: transparent;

    // Only set CSS gradients if enabled
    .cssgradients & {
        border-image:
            linear-gradient(to top right, $color 0, transparent 50%);
        border-image-slice: 1;
    }

    & > *:first-child {
        margin-top: 0;
    }

}
