.w-hero {
    background-color: #aaa;
}

.hero {

    // Improve header readability with a 'floor fade'
    @media #{$medium-up} {
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 12em;
            background: linear-gradient(
                to bottom,
                rgba(0, 0, 0, 0.4) 20%,
                rgba(0, 0, 0, 0)
            );
        }
    }

    @media #{$large-up} {
        min-height: 20em; //Safeguard to prevent pushed op content from leaking over top menu
    }

}
    .wp-post-image {
        width: 100%;
        height: auto;
        display: block;

    }

