.top-nav {
    margin-bottom: to-rem(12);

    @media #{$medium-up} {
            white-space: nowrap;
            margin-top: to-rem(12);
            margin-bottom: to-rem(12);
    }
}

    .top-nav__list {

        width: 100%;
        padding-left: 0;
        text-align: center;
        font-size: 0;

        @media #{$medium-up} {
            text-align: right;
        }

        // Styling for ALL li
        li {
            text-align: center;
            list-style-type: none;
            position: relative;
            font-size: $base-font-size;
        }

        // Styling for TOP-LEVEL li
        & > li {

            display: inline-block;
            // float: left;
            margin-left: 2%;
            // background-color: $color-cyan;
            border-bottom: 4px solid white;

            @media #{$medium-up} {
                box-shadow: 0 2px 2px -2px black;
            }

            @media #{$small-only} {
                // width: 25% - 2%;
            }

            &:first-child {
                margin-left: 0;
            }

            // Styling for ALL a
            a {
                text-decoration: none;
                display: block;
            }

            // Styling for TOP-LEVEL a
            & > a {

                transition: all .3s;
                color: white;
                font-weight: 500;

                // Button animation: http://tympanus.net/Development/CreativeButtons/
                &::after {
                    content: '';
                    position: absolute;
                    z-index: -1;
                    transition: all 0.3s;
                    width: 100%;
                    height: 0;
                    bottom: 0;
                    left: 0;
                    background: #fff;
                }

                @media #{$small-only} {
                    font-size: to-rem(14);
                    padding: to-rem(4) to-rem(4);
                }

                @media #{$medium-up} {
                    padding: to-rem(6) to-rem(8);
                }
            }

            @media #{$small-only} {
                &:hover > a,
                // & > a:active,
                &.current_page_item > a,
                &.current_page_ancestor > a{
                    color: darken($color-cyan, 20%);
                    background-color: white;
                }
            }

            @media #{$medium-up} {
                &:hover > a,
                // & > a:active,
                &.current_page_item > a,
                &.current_page_ancestor > a{
                    color: $color-link;
                    text-shadow: none;
                }

                &:hover > a::after,
                &.current_page_item > a::after,
                &.current_page_ancestor > a::after {
                    height: 100%;
                }
            }

            // Show dropdown on hover (only on non-touch devices)
            .no-touch &:hover .children {
                display: block;
            }


        }



        .children {
            display: none;
            text-shadow: none;
            position: absolute;
            right: 0;
            // width: 10em;
            top: 100%;
            background-color: white;
            padding-left: 0;
            white-space: nowrap;
            & > li {
                padding: to-rem(8);
            }
            a {
                color: darken($color-link, 0%);
            }
        }


    }


