@mixin side-content {
    @media #{$medium-up} {
        @include layout-debugger(magenta);
        float: right;
        margin: 0 0 to-rem(8) 0;
        width: percentage(5/12); // since sidebar is 3w (out of 12) and we want 3w overlap
        height: auto;
        padding-left: $half-column-gutter;
        padding-right: $half-column-gutter;
        clear: right;
    }

    @media #{$large-up} {
        width: percentage(4/9); // since sidebar is 3w (out of 9) and we want 1w overlap
        // @include layout-debugger(magenta);
        // width: 66.66666%; // since sidebar is 4w and content is 6w
        // // width: 4 * $column-width;
        // margin-right: -66.66666%;
    }
}

.content {
    margin-bottom: to-rem(16);

    @media #{$medium-up} {
        // Layout fixes
        & > * {
            max-width: percentage(9/12);
        }

        // Larger text on larger screens
        font-size: 1.05em;

    }
    @media #{$large-up} {
        & > * {
            max-width: percentage(6/9);
            // Because $collapse:true in layout
            padding-left: $half-column-gutter;
            padding-right: $half-column-gutter;
        }
        font-size: 1.1em; // Larger text on larger screens
        margin-top: -12em; // Leak content over header image
        min-height: 20em; // Min height to prevent sidenav from going off-canvas
        background-image: linear-gradient(180deg, fade-out($color-background, .15) 0, $color-background 11em);
        background-size: percentage(6/9) 100%;
        background-repeat: no-repeat;

    }

    a:active {
        position: relative;
        top: 1px;
    }

    // Enlarge the intro text
    & > p:first-of-type {
        font-size: 1.2em;

        @media #{$medium-up} {
            font-size: 1.3em;
        }

        @media #{$large-up} {
            font-size: 1.4em;
        }
    }

    .wp-caption {
        @include side-content;
        margin: 1em 0;
    }
    .wp-caption-text {
        font-size: to-rem(12);
        color: lighten($color-text, 40%);
        font-style: italic;
    }

    .alignright {
        @include side-content;
    }

}

    .content__breadcrumbs {
        color: darken($color-primary-header, 13%);
        margin: to-rem(16) 0 0 0;

        ul {
            padding-left: 0;
        }

        li {
            list-style-type: none;
            display: inline-block;
            padding: 0 to-rem(4);
            &::before {
                content: "» ";
            }
            &:first-child {
                padding: 0;
            }
            &:first-child::before {
                content: "";
            }
        }


    }

    .content__title {
        margin-top: 0;

        @media #{$medium-up} {
            font-size: 3em;
        }

    }

    .content__speak {
        text-align: center;
        clear: both;
    }

    .video-container {
        iframe {
            max-width: 100%;
        }
    }
