.side-nav {

    background-color: lighten($color-cyan, 35%);

    @media #{$large-up} {
        background-color: transparent;
        margin: to-rem(16) 0 to-rem(16) 0;
        font-size: 1.1em;
    }


    ul {
        padding-left: 0;
    }

    li {
        list-style-type: none;
        margin: to-rem(8) 0;

    }

    .current_page_item > a {
        font-weight: bold;
    }

    a {
        text-decoration: none;
        font-weight: 500;
        display: block;
        color: darken($color-link, 10%);

        &::before {
            content: "» ";
        }

        @media #{$small-only} {
            // font-size: to-rem(14);
            padding: to-rem(4) 0;
        }

        @media #{$medium-up} {
            padding: to-rem(6) to-rem(8);
        }

        @media #{$large-up} {
            color: $color-link;
        }
    }

    .children {
        padding-left: 2em;
    }
}
