$guides-box-height: to-rem(200);
$guides-box-padding: to-rem(12);

.guides {
    margin-top: to-rem(16);
    margin-bottom: to-rem(16);

    ul {
        list-style-type: none;
        padding-left: 0;
    }

    li {
        display: inline-block;
    }
}

.guides__inner-container {
    position: relative;
}

.guides__thumbnail {
    padding-top: to-rem(8);
    border-radius: 0 0 50% 50%;

    img {
        display: block;
        width: to-rem(48);
        height: to-rem(48);
        border-radius: 50%;
    }

    transition: all .5s ease;
    .wibaf-disabled & {
        filter: grayscale(1);
        opacity: .5;
    }
}

@mixin description-box-dimensions {
    width: 100%;
    padding: $guides-box-padding;
    height: $guides-box-height;
    overflow: auto;
    background-color: lighten($color-cyan, 30%);
}

.guides__description-box {
    @include description-box-dimensions;
    position: relative;
}

.guides__placeholder {
    text-align: center;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    color: lighten($color-cyan, 5%);
    font-size: 1.4em;
}

.guides__description {
    visibility: hidden;
    opacity: 0;
    transition: opacity .5s ease, visibility .5s ease;
    @include description-box-dimensions;
    position: absolute;
    left: 0;
    top: 0;
    color: darken($color-cyan, 35%);
    // margin-left: $half-column-gutter;
    // margin-right: $half-column-gutter;

    h3 {
        margin-top: 0;
        width: 100%;
    }
    .wibaf-disabled & h3::after {
        content: " (currently disabled)";
        color: fade-out($color-secondary-header, .3);
    }
}

.guides__button {
    @extend .button;
}

// Only show relevant button, hide the other
.wibaf-disabled .wibaf-guide-disable,
.wibaf-enabled .wibaf-guide-enable {
    display: none;
}

.guides__photo {
    display: none;
    img {
        width: $guides-box-height - 2 * $guides-box-padding;
        height: $guides-box-height - 2 * $guides-box-padding;
        border-radius: 50%;
    }
    float: right;

    @media #{$medium-up} {
        display: block;
    }
}

// When hovering over a photo, show the description
.guides li:hover {

    .guides__thumbnail {
        filter: none;
        opacity: 1;
        background-color: lighten($color-cyan, 30%);
    }

    .guides__description {
        visibility: visible;
        opacity: 1;
        transition: opacity .2s ease, visibility .2s ease;
    }
}
