.w-footer {
    background-color: darken($color-cyan, 5%);
}

.footer {
    color: white;
    // overflow: auto; // to prevent copyright margins from 'leaking' outside footer
}

.footer__menu {
    text-align: right;
    ul {
        list-style-type: none;
        padding-left: 0;
    }

    li {
        display: inline;
        &::before {
            content: " | ";
            font-weight: normal;
        }
        &:first-child::before {
            content: "";
        }
        &.current-menu-item {
            font-weight: bold;
        }

    }

    a {
        color: white;
    }

    @media #{$medium-up} {
        float: right;
        margin-top: to-rem(12);
    }
}

.elevator-container {
    text-align: center;
    margin-bottom: to-rem(12);

    @media #{$medium-up} {
        float: left;
    }
}

.elevator {
    width: auto;
    margin: 0 auto;
    cursor: pointer;
    display: inline-block;

    svg {
        display: inline;
        vertical-align: middle;
    }
    span {
        vertical-align: middle;
    }
}
