@import "content"; // To load the side-content mixin

.tip {
    @include side-content;
    margin: 1.5em 0;
    display: none;

    blockquote {
        $border-width: 5px;
        $border-color: $color-cyan;

        margin: 0 0 to-rem(16) 0;
        padding: 0;
        font-size: to-rem(18);
        border-bottom: $border-width solid $border-color;
        position: relative;

        // Speech bubble effect
        // @source http://nicolasgallagher.com/pure-css-speech-bubbles/demo/
        &::before {
            content: "";
            position: absolute;
            bottom: -20px; // value = - border-top-width - border-bottom-width
            left: 40px; // controls horizontal position
            border-width: 20px 20px 0;
            border-style: solid;
            border-color: $border-color transparent;//  reduce the damage in FF3.0
            display: block;
            width: 0;
        }
        // creates the smaller  triangle
        &::after {
            content: "";
            position: absolute;
            bottom: -13px; // value = - border-top-width - border-bottom-width
            left: 47px; // value = (:before left) + (:before border-left) - (:after border-left)
            border-width: 13px 13px 0;
            border-style: solid;
            border-color: $color-background transparent;/* reduce the damage in FF3.0 */
            display: block;
            width: 0;
        }

        @media #{$large-up} {
            font-size: to-rem(20);
        }

    }



    cite {
        font-size: to-rem(16);
        font-style: italic;
    }

    img {
        border-radius: 50%;
        width: to-rem(36);
        height: to-rem(36);
        display: inline;
        vertical-align: middle;
        margin-right: to-rem(8);
    }

}

.tip__hide {
    color: lighten($color-text, 40%);
    font-size: .8em;
    cursor: pointer;
    text-align: right;

    &::before {
        content: "×";
        font-weight: bold;
        padding-right: .2em;
    }
}

.wibaf-tip-show {
    display: block;
}

.wibaf-tip-disabled {
    display: none;
}
