.w-header {
    @media #{$small-only} {
        background-color: darken($color-cyan, 15%);
    }
    @media #{$medium-up} {
        position: absolute; // from medium on, overlay hero with header
    }
    width: 100%;
    z-index: 20;
}

.header {
    @media #{$medium-up} {
        text-shadow: 0 0 2px black; // when overlaying the hero, add a text shadow
    }
}
    .header__title {
        font-size: to-rem(20);

        @media #{$medium-up} {
            font-size: to-rem(24);
            margin-top: to-rem(16);
        }

        a {
            color: white;

            &:active {
                position: relative;
                top: 2px;
            }
        }

        span {
            color: lighten($color-pink, 80%);
        }
    }
