.home-topics {
//     display: flex;
//     flex-wrap: wrap;
    width: 100%;
    font-size: 0;
    vertical-align: top;
    @include clearfix;
    clear: both;
}

    a.home-topics__item {
        // flex-basis: 30%;
        background-color: darken($color-cyan, 20%);
        margin-bottom: to-rem(16);
        width: 100%;
        display: block;
        float: left;
        font-size: $base-font-size;
        height: to-rem(200);
        position: relative;
        padding: to-rem(16);

        // background-image: url("../../../dummy_content/ehv.jpg");
        background-size: auto 100%;
        background-position: center center;
        background-repeat: no-repeat;

        filter: grayscale(1);
        transition: filter .5s ease, background-size .5s ease;
        -webkit-font-smoothing: antialiased; // to fix glitches with the filter

        &:hover {
            filter: none;
            transition: filter .2s ease, background-size .2s ease;
            background-size: auto 105%;
        }

        h2 {
            position: absolute;
            bottom: 0;
            color: white;
            text-shadow: 0 0 to-rem(8) black;
        }

        img {
            object-fit: cover;
        }

        @media #{$medium-up} {
            // Create a two-column layout
            width: 48%;
            margin-right: 4%;

            &:nth-of-type(even) {
                margin-right: 0;
            }
        }

    }
