// CSS follows SMACSS principles.
// Main difference: here, modules are called *partials* instead.

// Naming conventions follow BEM (block, element modifier)
// This way: block__element--modifier
// A name may contain single hypens.
// @see http://csswizardry.com/2013/01/mindbemding-getting-your-head-round-bem-syntax/

@charset "UTF-8";

$debug: false;

// Modules
// Modules do not output anything.
// They are indepent of each other, with the exception of
// the dependence on the functions module
@import "modules/normalize";
@import "modules/normalize-opentype";
@import "modules/functions";
@import "modules/variables";
@import "modules/fonts";
@import "modules/grid";
@import "modules/media-queries";
@import "modules/elements";
// @import "modules/wordpress_core";
@import "modules/print";

@import 'base';
@import 'layout';

// Partials
@import "partials/hero";
@import "partials/top-nav";
@import "partials/side-nav";
@import "partials/content";
@import "partials/header";
@import "partials/footer";
@import "partials/home-topics";
@import "partials/next-page";
@import "partials/guides";
@import "partials/tip";
